.page{
	background: none;
}
.achievementList .content .mainList{
	width: 100%;
	margin-top: 30px;
}
.mainList .caseList ul{
	width: 100%;
	overflow: hidden;
}
.mainList .caseList ul li{
	width: 23%;
	height: 0;
	padding-top: 23%;
	float: left;
	position: relative;
	margin: 0 1%;
	margin-bottom: 20px;
	-webkit-transition: background-color .3s linear;
	-moz-transition: background-color .3s linear;
	-ms-transition: background-color .3s linear;
	-o-transition: background-color .3s linear;
	transition: background-color .3s linear;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	-o-box-sizing: border-box;
	box-sizing: border-box;
	cursor: pointer;
}
.mainList .caseList ul li:hover{
	background-color: #fcf8df;
	-webkit-box-shadow: 0px 1px 3px rgba(0,0,0,.18);
	-moz-box-shadow: 0px 1px 3px rgba(0,0,0,.18);
	-ms-box-shadow: 0px 1px 3px rgba(0,0,0,.18);
	-o-box-shadow: 0px 1px 3px rgba(0,0,0,.18);
	box-shadow: 0px 1px 3px rgba(0,0,0,.18);
}
.mainList .caseList ul li .outBox{
	width: 94%;
	height: 94%;
	position: absolute;
	top: 3%;
	left: 3%;
}
.mainList .caseList ul li .outBox .vedioImg{
	width: 100%;
	height: 0;
	padding-top: 56.25%;
	position: relative;
}
.mainList .caseList ul li .outBox .vedioImg img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.mainList .caseList ul li .outBox h3{
	color: #020202;
	font-size: 1em;
	margin: 10px 0 8px;
	font-weight: 600;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.mainList .caseList ul li .outBox .author{
	font-size: .75em;
	display: block;
	margin-bottom: 4px;
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	float: left;
	color: #a7a7a7;
}
.mainList .caseList ul li .outBox p{
	color: #3e3c3b;
	font-size: .75em;
	display: -webkit-box;
    overflow: hidden;
    white-space: normal !important;
    text-overflow: ellipsis;
    word-wrap: break-word;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
.mainList .caseList ul li .outBox .data .like{
	font-size: .75em;
	display: block;
	margin-top: 8px;
	color: #a7a7a7;
	float: left;
}
.mainList .caseList ul li .outBox .data .review{
	font-size: .75em;
	display: block;
	margin-top: 8px;
	color: #a7a7a7;
	float: left;
	margin-right: 16px;
}






























































